* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell,
    'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

.container {
  width: 1680px;
  padding: 0 15px;
  margin: 0 auto;
}

.search-form {
  display: flex;
  justify-content: center;
  margin-top: 30px;
  margin-bottom: 50px;
}

.load-more {
  display: block;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 30px;
}

.is-hiden {
  display: none;
}

.gallery {
  display: flex;
  flex-wrap: wrap;
}

.photo-card {
  width: 370px;
  margin-right: 30px;
  margin-bottom: 30px;

  box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.25);
  border-radius: 0 0 4px 4px;
}

.gallery .photo-card:nth-child(4n) {
  margin-right: 0;
}

.photo-card img {
  display: block;
  width: 100%;
  height: 200px;
  object-fit: cover;
}

.info {
  display: flex;
  justify-content: space-around;
}

.info-item {
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
}
